        *,
         ::after,
         ::before {
            margin: 0;
            padding: 0;
            box-sizing: border-box;
        }
        
        a {
            text-decoration: none;
        }
        
         ::selection {
            text-shadow: none;
            color: #fff;
            background: #555;
        }
        
        body {
            background-image: url(./img/wood.png);
            background-repeat: repeat;
        }
        
        #root {
            width: 100%;
            min-height: 100vh;
            display: flex;
            justify-content: center;
            align-items: center;
            overflow: hidden;
        }
        
        .main-header {
            height: 700px;
            background-color: #f3f3f3;
            position: relative;
        }
        
        .col-xxl-4 {
            max-width: 550px;
        }
        
        .profile-header {
            width: 100%;
            height: 100%;
        }
        
        .profile-child {
            position: absolute;
            top: 0;
            right: 100%;
            box-shadow: -4px 4px 4px rgb(0 0 0 / 20%), 1px 1px 4px rgb(0 0 0 / 13%);
            transform: perspective(1800px) rotateY(180deg);
            transform-style: preserve-3d;
            z-index: 2;
            transform-origin: right center;
            transition: all 0.6s ease-in-out, height 0s;
        }
        
        .box-shadow-orginal {
            overflow-y: scroll;
            box-shadow: 0 5px 4px -2px rgb(0 0 0 / 20%);
        }
        
        .scroll-transi {
            width: calc(100% + 15px);
            overflow-y: scroll;
            transition: all .5s;
            overflow-x: hidden;
        }
        
        .scroll-transi:hover {
            width: calc(100%);
        }
        
        .shadow-box-main {
            box-shadow: 0 6px 4px -2px rgb(0 0 0 / 20%);
        }
        
        .contact-header::-webkit-scrollbar,
        .resume-header::-webkit-scrollbar,
        .profile-header::-webkit-scrollbar {
            width: 8px;
            height: 10px;
        }
        
        .contact-header::-webkit-scrollbar-thumb,
        .resume-header::-webkit-scrollbar-thumb,
        .profile-header::-webkit-scrollbar-thumb {
            background: #8787878a;
            border-radius: 5px;
        }
        /* PORTFOLIO */
        
        .scroll-transi:hover .nexsibili-transi {
            width: calc(100% + 15px);
        }
        
        .nexsibili-transi {
            width: calc(100%);
            overflow-x: hidden;
            transition: all .5s;
        }
        
        .profile-div {
            padding: 2rem;
            position: relative;
        }
        
        .profile-title {
            text-align: center;
            margin: 25px 0;
            padding: 10px 0;
        }
        
        .title-h2 {
            position: relative;
        }
        
        .title-h2::after {
            content: '';
            width: 100%;
            height: 1px;
            background-color: #c8c8c8;
            position: absolute;
            top: 20px;
            left: 0;
            border-radius: 1px;
        }
        
        .title-h2>strong {
            background: #333333;
            color: white;
            position: relative;
            z-index: 1;
            font-weight: 800;
            padding: 0.2em 0.4em;
            border-radius: 4px;
            text-transform: uppercase;
        }
        /* ul li */
        
        .profile-menu {
            display: flex;
            justify-content: center;
            align-items: center;
            margin: 30px 0;
        }
        
        .profile-menu>ul {
            list-style: none;
            display: flex;
            font-size: 18px;
            color: #333333;
            justify-content: center;
        }
        
        .profile-menu>ul>li {
            margin: 0 15px;
            cursor: pointer;
            padding: 5px 0;
            transition: all .5s;
            border-bottom: 1px solid #33333300;
            text-transform: capitalize;
        }
        
        .profile-menu>ul>li:hover {
            border-bottom: 1px solid #333333;
        }
        /*  portfolio-header*/
        
        .portfolio-figure-header {
            /* max-width: 210px; */
            position: relative;
            padding: 0;
            margin: 10px 2px;
            background-color: #fff;
            border-radius: 3px;
            box-shadow: 0 -10px 13px #d1d1d1;
            cursor: pointer;
            border-bottom: 3px solid #ddd;
            transition: all 0.3s;
        }
        
        .portfolio-figure-header img {
            width: 100%;
            display: flex;
            margin: 0 auto;
            background-size: cover;
            align-content: center;
            justify-content: center;
            border: 3px solid white;
            filter: grayscale(75%);
            transition: all 0.3s;
            border-radius: 5px;
        }
        
        .portfolio-figure-header:hover {
            box-shadow: 0 0 13px #c1c1c1;
        }
        
        .portfolio-figure-header:hover img {
            filter: grayscale(0);
        }
        
        .title-portolio {
            width: 100%;
            padding: 15px 5px 10px;
            text-align: center;
            display: flex;
            flex-flow: column wrap;
            min-height: 90px;
            justify-content: space-evenly;
        }
        
        .title-portolio figcaption {
            color: #222;
            font-size: 16px;
        }
        
        .title-portolio span {
            /* width: 100%; */
            display: block;
            padding: 6px 0 5px;
            color: #888;
            text-shadow: 0 1px 0 #fff;
            font-size: 15px;
        }
        /* contact-us */
        /* .contact-us::-webkit-scrollbar {
            width: 8px;
            height: 10px;
        } */
        
        .contact-us::-webkit-scrollbar-thumb {
            background: #8787878a;
            border-radius: 5px;
        }
        
        .contact-us:hover .contact-us::-webkit-scrollbar {
            width: 8px;
            height: 10px;
        }
        
        .contact-us {
            position: absolute;
            right: 0%;
            top: 0;
            background: #fafafa;
            box-shadow: 2px 2px 4px rgb(0 0 0 / 20%), -2px 2px 4px rgb(0 0 0 / 13%);
            /* overflow-y: scroll; */
            /* overflow-x: hidden; */
            transform-origin: right center;
            transition: all 0.6s ease-in-out, height 0s;
            /* transition-delay: 0.4s; */
            transform-style: preserve-3d;
            transform: perspective(1800px) rotateY(0deg);
            z-index: 2;
        }
        
        .contact-header {
            width: 100%;
            height: 700px;
            overflow-y: scroll;
            position: absolute;
            right: 0;
            top: 0;
            background: #fafafa;
            box-shadow: -4px 4px 4px rgb(231 231 231 / 13%), 1px 1px 4px rgb(175 175 175 / 11%);
            /* opacity: 0; */
            /* transform: rotateY(180deg); */
            /* transition-delay: ; */
            transform: rotateY(180deg) translate3d(0, 0, 1px);
            backface-visibility: visible;
            transition: all .6s cubic-bezier(0.64, -0.03, 0.02, 0.96);
        }
        
        .background-contact {
            width: 100%;
            height: 100%;
            background: url('./img/rahmat-back.JPG');
            background-size: cover;
            background-position: 40% 50%;
            position: absolute;
            top: 0;
            left: 0;
            transform: rotateY(180deg);
            display: flex;
            flex-flow: column wrap;
            /* z-index: 2; */
            filter: grayscale(1);
            transform: rotateY(0deg) translate3d(0, 0, 0);
            backface-visibility: hidden;
        }
        
        #close {
            width: 50px;
            height: 60px;
            background: #333;
            color: white;
            display: flex;
            justify-content: space-around;
            align-items: end;
            position: absolute;
            top: 0;
            right: 10px;
            padding: 3px 5px;
            border-radius: 1px;
            box-shadow: -10px 0 14px #7070701c, 10px 0 14px #7070701c;
            cursor: pointer;
            overflow: hidden;
            border-radius: 0 0 5px 5px;
            transition: all .5s;
        }
        
        #close:hover {
            color: #bababa;
            height: 65px;
        }
        
        #close:hover .close-span {
            top: -3px;
        }
        
        #close::after {
            content: '';
            position: absolute;
            bottom: -5px;
            right: 0;
            border: 25px solid transparent;
            border-bottom-color: #ebebeb;
            border-left-color: rgb(0 76 175 / 0%);
            border-top: none;
        }
        
        #close::before {
            content: '';
            width: 70%;
            height: 70%;
            position: absolute;
            top: 0;
            left: 7px;
            border-right: 2px dashed #c5c5c5;
            border-left: 2px dashed #c5c5c5;
        }
        
        .close-span {
            font-weight: 600;
            font-size: 35px;
            transform: rotate(45deg);
            position: absolute;
            top: -8px;
            left: 15px;
            transition: all .3s;
        }
        
        .social-header {
            display: flex;
            flex-flow: column wrap;
            justify-content: space-around;
            align-items: center;
            margin: 25px 0;
            min-height: 130px;
        }
        
        .social-header>p:nth-of-type(1) {
            color: #333;
            font-size: 14px;
            padding: 15px 0;
            font-weight: 500;
        }
        
        .box-line {
            display: flex;
            justify-content: center;
            align-items: center;
            margin: 25px;
            padding: 3px 12px;
            border: 1px dashed #ccc;
            font-size: 16px;
            font-weight: 700;
        }
        /* .social-link-header {} */
        
        .href-social {
            padding: 6px 8px 7px 8px;
            display: flex;
            justify-content: center;
            align-items: center;
            width: 45px;
            height: 45px;
            border: 1px solid #ddd;
            border-radius: 50%;
            font-size: 20px;
            color: #444;
            margin: 10px;
            transition: all .5s;
        }
        
        .href-social:hover {
            transform: scale(1.2);
            box-shadow: 0 0 10px #d1d1d1;
        }
        
        .href-social>i {
            display: flex;
            align-items: center;
            justify-self: center;
        }
        
        .img-line {
            width: 100%;
            height: 10px;
            margin: 35px 0;
            display: flex;
            justify-content: center;
            align-items: center;
            background: url('./img/imgLine.png') left top repeat-x;
            background-size: 60px 10px;
        }
        /* .img-line>img{

} */
        /* profile header cartpostal */
        
        .profile-title-rahmat>h1 {
            font-size: 25px;
            font-weight: 700;
            text-shadow: 0 0 15px #2532516b;
            text-transform: capitalize;
            text-align: left;
        }
        
        .profile-title-rahmat {
            text-align: left;
            padding-left: 0;
        }
        
        .profile-title-rahmat>p strong {
            color: #575757;
            font-size: 17px;
            margin: 10px 0;
            font-weight: 600;
            display: block;
            text-transform: capitalize;
        }
        
        .profile-title-rahmat p>a {
            color: #C25337;
            font-size: 15px;
            margin: 15px 0;
            text-align: left;
            font-weight: 500;
        }
        
        .profile-postal-header {
            justify-content: center;
            align-items: center;
            display: flex;
        }
        
        .profile-postal {
            width: 178px;
            height: 100%;
            display: flex;
            align-items: center;
            justify-content: flex-start;
            padding-left: 12px;
            margin-left: auto;
            background: url('./img/stamp.png') no-repeat;
            margin-top: 20px;
            position: relative;
            background-position: center;
            background-size: contain;
        }
        
        .profile-postal>img {
            width: 90%;
            height: auto;
            display: flex;
            align-items: center;
            justify-content: center;
            position: relative;
        }
        
        .profile-postal::after {
            content: '';
            width: 90%;
            height: 100%;
            background: url('./img/stamp-mask.png');
            position: absolute;
            top: -7px;
            left: 3px;
        }
        /* form-header */
        
        .form-header {
            width: 100%;
            display: flex;
            flex-flow: column wrap;
            margin-top: 40px;
        }
        
        .form-header>.row {
            margin: 10px 0;
        }
        
        .form-header>.row>input,
        .form-header>.row>textarea {
            font-size: 15px;
            padding: 12px 16px;
            color: #345;
            border: 1px solid rgba(0, 0, 0, 0.1);
            outline: none;
        }
        
        .form-header>.row>label {
            letter-spacing: 1px;
            font-size: 13px;
            color: #333;
            text-transform: capitalize;
            font-weight: 500;
            padding: 10px 0;
        }
        
        .submit-form {
            width: 80px;
            height: 35px;
            display: flex;
            justify-content: center;
            align-items: center;
            border: 1px solid rgb(0 0 0 / 9%);
            font-size: 21px;
            color: #333;
            border-radius: 5px;
            box-shadow: 0 0 11px #bcbcbcbd;
            margin-top: 25px;
            margin-left: auto;
            transition: all .5s;
            position: relative;
            overflow: hidden;
        }
        
        .submit-form svg {
            position: absolute;
            top: 7px;
            left: -30px;
            display: flex;
            justify-content: center;
            align-items: center;
            transition: all .5s;
            color: rgb(35 53 94);
        }
        
        .submit-form span {
            transition: all .5s;
        }
        
        .submit-form:hover span {
            transform: translateX(65px);
        }
        
        .submit-form:hover svg {
            left: 30px !important;
        }
        /* RESUME */
        
        .resume-header {
            height: 700px;
            overflow-y: scroll;
            position: absolute;
            right: 0;
            top: 0;
            background: #fafafa;
        }
        
        .box-aboutme {
            margin-top: 33px;
            padding: 0;
            display: flex;
            flex-flow: row wrap;
            justify-self: center;
            align-items: center;
        }
        
        .box-aboutme>p {
            text-align: left;
            font-size: 17px;
        }
        
        .underline {
            position: relative;
        }
        
        .underline::after {
            content: '';
            width: 100%;
            height: 2px;
            background: rgba(0, 0, 0, 0.6);
            border-radius: 3px;
            position: absolute;
            bottom: -3px;
            left: 0;
            z-index: 0;
        }
        
        .emplyment-header {
            padding-right: 0;
            display: flex;
            justify-content: center;
            align-items: center;
            /* margin-bottom: 25px; */
        }
        
        .experience-years {
            min-width: 100px;
            height: 100px;
            position: relative;
            display: flex;
            justify-content: center;
            align-items: center;
            border: 1px dashed #333333;
            padding: 5px;
            margin-top: 7px;
            background-color: white;
        }
        
        .experience-years::before {
            content: '';
            width: 1px;
            height: 39px;
            border: 1px dashed #333333;
            position: absolute;
            left: 50%;
            top: 100px;
        }
        
        .emplyment-header:last-child .experience-years::before {
            content: '';
            display: none;
        }
        
        .experience-title {
            font-size: 18px;
            text-transform: capitalize;
        }
        
        .experience-compuni {
            /* padding-left: 30px; */
            position: relative;
            margin-bottom: 5px;
            font-size: 14px;
            color: #888888;
            font-weight: 500;
        }
        
        .experience-compuni>i {
            font-size: 18px;
        }
        
        .experience-aboute {
            width: 100%;
            margin-bottom: 0;
            color: #1e1e1e;
            font-size: 15px;
            padding: 5px 0;
        }
        
        .downlodcv {
            display: flex;
            justify-content: center;
            align-items: center;
            padding: 15px;
            background-color: #333;
            width: 150px;
            border-radius: 22px;
            color: white;
            text-decoration: none;
            margin: auto;
            transition: all .5s;
        }
        
        .downlodcv:hover {
            background-color: #0a58ca;
            box-shadow: 0 0 15px rgba(61, 36, 36, 0.32);
            transform: scale(1.2);
            color: white;
        }
        /*  */
        
        .develops-header {
            margin: 20px 0;
        }
        
        .progress-img-header,
        .progress-header {
            padding-left: 0;
        }
        
        .progress-header>p:nth-of-type(1) {
            display: flex;
            flex-flow: row wrap;
            justify-content: space-between;
            align-items: center;
            font-size: 15px;
            color: #333;
            font-weight: 600;
            margin-bottom: 5px;
        }
        
        .progres {
            width: 100%;
            height: 5px;
            background-color: #a6a6a6;
            border-radius: 3px;
            position: relative;
        }
        
        .progres-loader {
            height: 5px;
            border-radius: 3px;
            position: absolute;
            left: 0;
            top: 0;
            background: #333;
            transition: all 2s;
        }
        /* traansio open profile */
        
        .background-resume {
            width: 100%;
            height: 700px;
            background: url('./img/rat.jpg');
            background-size: cover;
            background-position: 40% 50%;
            position: absolute;
            top: 0;
            left: 0;
            display: flex;
            flex-flow: column nowrap;
            transform: rotateY(180deg) translate3d(0, 0, 2px);
            backface-visibility: hidden;
            transition: all .3s;
        }
        
        .title-name {
            width: 100%;
            display: flex;
            flex-flow: column wrap;
            justify-content: center;
            align-items: center;
        }
        
        .title-name>h1 {
            margin: 30px auto 10px;
            color: white;
            font-size: 3em;
            text-shadow: #000000 -1px 1px 0, #000000 -2px 2px 0, #d45848 -3px 3px 0, #d45848 -4px 4px 0;
            text-transform: capitalize;
            letter-spacing: 2px;
        }
        
        .title-name>p {
            text-align: center;
            color: white;
            font-size: 1.5em;
            letter-spacing: 1px;
            text-shadow: rgb(0 0 0 / 42%) 1px 1px 0, rgb(0 0 0 / 40%) 2px 2px 0;
        }
        
        .info-rahmat {
            padding-left: 20px;
        }
        
        .title-and-qz {
            margin: 20px 5px;
        }
        
        .title-and-qz>h2 {
            font-weight: 700;
            color: #FC0;
            font-size: 25px;
            text-shadow: rgb(0 0 0 / 40%) 1px 1px 0, rgb(0 0 0 / 40%) 2px 2px 0;
            margin: 0;
        }
        
        .title-and-qz>p {
            color: white;
            font-size: 26px;
            padding: 5px;
            letter-spacing: 1px;
            text-shadow: rgb(0 0 0 / 40%) 1px 1px 0, rgb(0 0 0 / 40%) 2px 2px 0;
            font-weight: 500;
        }
        
        .click-open-card {
            display: flex;
            justify-content: end;
            align-items: center;
            padding: 0;
        }
        
        #open {
            width: 80px;
            height: 46px;
            display: flex;
            justify-content: center;
            align-items: center;
            text-align: center;
            background: #d64b4b;
            /* margin-bottom: 85px; */
            transition: all .4s ease-out;
            position: relative;
            cursor: pointer;
        }
        
        #open::after {
            content: '';
            position: absolute;
            left: -30px;
            top: 0;
            height: 100%;
            display: flex;
            border: 23px solid #d64b4b;
            border-left-color: transparent;
            border-bottom-color: rgb(214 75 75);
            transition: all .4s ease-out;
        }
        
        #open:hover#open::after {
            left: -37px;
        }
        
        #open:hover #open-span {
            padding-right: 10px;
        }
        
        #open::before {
            content: '';
            width: 125%;
            height: 100%;
            display: flex;
            z-index: 1;
            position: absolute;
            right: 0;
            top: 0;
            box-shadow: rgb(0 0 0 / 40%) 1px 1px 1px, rgb(0 0 0 / 40%) 2px -1px 1px;
        }
        
        #open>span {
            padding: 0 15px 0 10px;
            position: relative;
            font-size: 15px;
            font-weight: bold;
            line-height: 42px;
            min-height: 42px;
            color: #811919;
            text-shadow: 0 1px 0 rgb(255 255 255 / 20%);
            text-transform: uppercase;
        }
        
        #open-span {
            transition: all .4s ease-out;
        }
        
        #open>span::after {
            content: '';
            width: 140%;
            height: 95%;
            display: flex;
            justify-content: center;
            align-items: center;
            position: absolute;
            top: 0;
            right: -5px;
            border: 1px dashed rgb(0 0 0 / 25%);
            border-right: none;
            border-left: none;
            margin-top: 2px;
            z-index: 2;
        }
        /* h700 z1 */
        
        .h700-z1 {
            transition: all .6s cubic-bezier(0.64, -0.03, 0.02, 0.96);
            /* height: 700px; */
            z-index: 1;
            opacity: 1;
        }
        
        .transition-delay3 {
            transition-delay: 0.3s;
        }
        
        .transition-delay6 {
            transition-delay: 0.6s;
        }
        
        .rorarey0 {
            z-index: 1;
            transform: perspective(1800px) rotateY(0deg);
        }
        
        .rorarey180 {
            z-index: 3;
            transform: perspective(1800px) rotateY(180deg);
        }
        
        @media (min-width:1090px) {
            .resume-header {
                transform: rotateY(0deg) translate3d(0, 0, 0);
                backface-visibility: visible;
                transition: all .6s cubic-bezier(0.64, -0.03, 0.02, 0.96);
            }
            .navbar-parent {
                display: none;
            }
        }
        
        @media (max-width:1200px) and (min-width:1090px) {
            .title-name>h1 {
                margin: 30px auto 15px;
                font-size: 2.7rem;
            }
            .main-header {
                max-width: 365px;
            }
            .emplyment-header {
                margin-bottom: 10px;
            }
            .experience-years::before {
                height: 75%;
            }
        }
        
        @media (max-width:1090px) {
            .visibility-hidden {
                transition: all 1s;
                opacity: 0;
                visibility: hidden;
            }
            .profile-div {
                padding-top: 0;
            }
            /* navbar */
            .navbar-parent {
                width: 100%;
                height: 55px;
                z-index: 1;
                position: relative;
                top: 0;
                left: 0;
                display: flex;
                justify-content: center;
                align-items: inherit;
            }
            .nav-ul {
                width: 100%;
                height: 100%;
                display: flex;
                flex-flow: row nowrap;
                justify-content: center;
                align-items: center;
                padding: 0;
                margin: 0;
                position: relative;
            }
            .nav-ul::after {
                content: '';
                width: 25%;
                height: 100%;
                display: flex;
                position: absolute;
                background-color: #15b79c;
                left: var(--boxAfterLeft, 25%);
            }
            .nav-ul>li {
                height: 100%;
                padding: 10px;
                display: flex;
                flex-flow: row nowrap;
                background-color: #303335;
                color: #ddd;
                justify-content: center;
                align-items: center;
                transition: all .5s;
                border-right: 1px solid #78797b;
                box-shadow: inset 0px 0px 5px #6c6c6c80, 0px 3px 8px rgb(75 75 75 / 25%);
            }
            .nav-ul>li:hover {
                color: rgb(248, 248, 248);
                background: #222;
            }
            .nav-ul>li>svg {
                position: relative;
                z-index: 1;
            }
        }